
          @import "@/assets/css/common.scss";
          @import "@/assets/css/fontface.scss";
        

$section-margin-top: 112px;

.home-wrapper {
  .main-title {
    width: 92vw;
    min-height: 260px;
    opacity: 0.98;
    padding-top: 64px;
    .title {
      padding: 0 48px;
      text-transform: uppercase;
    }
    .by {
    }
    .label-area {
      margin-top: $section-margin-top;
      .label-title {
      }

      .label-list {
        margin-top: 24px;
        padding: 24px 24px;

        border-top: 1px solid $color-primary;
        border-bottom: 1px solid $color-primary;

        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 12px 16px;
        label {
          background-color: $color-primary;
          color: $color-primary-white;
          padding: 4px 8px;
        }
        &.invert {
          background-color: $color-primary;
          border-color: $color-primary-white;
          label {
            background-color: $color-primary-white;
            color: $color-primary;
          }
        }
      }
    }
  }
  .post-area {
    padding-top: calc($section-margin-top * 2);
    background-color: $color-primary-white;
    .post-title {
    }
    .post-list {
      padding-top: 64px;
      padding-bottom: 64px;
      width: 92vw;

      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-auto-columns: auto;
      .post {
        padding: 32px;
        box-shadow: 0 0 0 0px $color-primary inset; 
        box-sizing: border-box;

        word-break: keep-all;

        cursor: pointer;
        user-select: none;
        transition: .2s;;

        .name {
          font-weight: 500;
          height: 4rem;
          transition: .2s;
        }
        .tag-list {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          gap: 4px 8px;
          label {
            padding: 2px 3px 2px 4px;
            transition: .2s;
          }
        }
        &:hover {
        box-shadow: 0 -3px 0 $color-primary inset; 
          .name {
            transform: scale(1.1, 1.1);
            color: black;
          }
        }
      }
    }
  }
}

// modile
@media screen and (max-width: 480px) {
  .post-list {
    grid-template-columns: 1fr;
  }
}
@media (min-width: 1280px) and (max-width: 4000px) {
  .main-title {
    .title {
      padding-top: 64px !important;
    }
    .label-area {
      margin-top: calc($section-margin-top * 2) !important;
    }
  }
  .post-list {
    grid-template-columns: 1fr 1fr 1fr 1fr !important;
    .post {
      .name {
        font-size: 1.2rem;
      }
    }
  }
}

