
          @import "@/assets/css/common.scss";
          @import "@/assets/css/fontface.scss";
        

$section-margin-top: 112px;

.background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;

  .panel {
    width: 100%;
    height: 50vh;
    max-height: 360px;
    background-color: $color-primary;
  }

  .words {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

    // mix-blend-mode: exclusion;
    mix-blend-mode: screen;
    font-size: 4rem;
    line-height: 0.85;
    font-weight: 600;
    text-align: left;
    color: #1d1d1d;

    .sentense {
      position: relative;
      width: 100%;
      text-align: justify;
      // text-align-last: justify;
      word-break: keep-all;
      text-transform: uppercase;
    }
  }
}
.frame {
  display: flex;
  flex-direction: column;
  align-items: center;

  .body {
    width: 92vw;
    margin-top: 16vh;
    margin-bottom: calc($section-margin-top * 1);
    min-height: 50vh;
    box-shadow: 2px 2px 8px #1d1d1d11;
    background: linear-gradient(
      rgba(255, 255, 255, 0.85) 64px,
      $color-primary-white calc($section-margin-top * 2)
    );
    transition: margin .2s;
  }
}

@media (min-width: 1280px) and (max-width: 4000px) {
  .background {
    .panel {
      max-height: 560px;
    }
  }

  .frame {
    .body {
      margin-top: 24vh;
    }
  }
}
